import * as React from 'react';
import Grid from '@mui/material/Grid';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Menu from '@mui/material/Menu';
import MenuIcon from '@mui/icons-material/Menu';
import Container from '@mui/material/Container';
import Button from '@mui/material/Button';
import MenuItem from '@mui/material/MenuItem';

const pages = ['Home', 'Services','Experience', 'Works', 'Contact'];

function ResponsiveAppBar() {


  return (
    <AppBar position="static" sx={{background:'var(--color-accent1)' 
    , borderBottom:'var(--border)' , boxShadow:'unset'}}>
      <Container >
        <Toolbar disableGutters>
          <Typography
            noWrap
            component="a"
            href="#"
            sx={{
              mr: 2,
              display: { xs: 'none', md: 'flex' },
              fontWeight: 700,
              letterSpacing: '.3rem',
              color: 'inherit',
              textDecoration: 'none',
              fontSize:'2rem',
            }}
          >
            Farimah
            <Typography sx={{fontSize:'2rem', color:'var(--color-red)'}}>
                .
            </Typography>
          </Typography>

    
          <Box sx={{flexGrow: 1, display: { xs: 'flex', md: 'flex' } , justifyContent:{xs:'space-between', md:'end'}}}>
          <Typography href='#about-me' component="a" fontWeight='bold' sx={{ my: 2, color: 'white', fontSize:'1rem', fontWeight:'bold', px:'1.5rem' , textDecoration:'none' , display: { xs: 'none', md: 'block' }}}>
          About Me
            </Typography>
            <Typography href='#services' component="a" fontWeight='bold' sx={{ my: 2, color: 'white', display: 'block', fontSize:{xs:'.8rem' , md:'1rem'} , fontWeight:'bold', px:'1.5rem' , textDecoration:'none'}}>
            Services
            </Typography>
            <Typography href='#experience' component="a" fontWeight='bold' sx={{ my: 2, color: 'white', display: 'block', fontSize:{xs:'.8rem' , md:'1rem'}, fontWeight:'bold', px:'1.5rem' , textDecoration:'none'}}>
            Experience
            </Typography>
            <Typography href='#recent-works' component="a" fontWeight='bold' sx={{ my: 2, color: 'white', display: 'block', fontSize:{xs:'.8rem' , md:'1rem'}, fontWeight:'bold', px:'1.5rem' , textDecoration:'none'}}>
            Recent Works
            </Typography>
          </Box>
        </Toolbar>
      </Container>
    </AppBar>
  );
}
export default ResponsiveAppBar;
