import React from 'react';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box'
import Container from '@mui/material/Container';
import DesignServicesIcon from '@mui/icons-material/DesignServices';
import CodeIcon from '@mui/icons-material/Code';
import SchoolIcon from '@mui/icons-material/School';
import IconButton from '@mui/material/IconButton';
import Divider from '@mui/material/Divider';

const Experience = () => {
    return (
        <Container id="experience" sx={{ mt: '4rem' }}>
            <Typography component="span" fontWeight='bold' sx={{ fontSize: { xs: '1.4rem', md: '2rem' } }}>
                Experience
            </Typography>
            <Grid sx={{ display: 'flex', gap: '2rem', flexDirection: { xs: 'column', md: 'row' }}}>
            <Box className='experience-box' sx={{ position:'relative' , background:'var(--color-purpel)' , p:'2rem' , borderRadius:'1rem' , display: 'flex', gap: '2rem', flexDirection: { xs: 'column', md: 'column' }, mt: '2rem' }}>
                {/* <Divider sx={{background:'var(--color-red)'}} orientation="vertical" flexItem /> */}
                <Box  sx={{display:'flex' , flexDirection:'row' ,  gap:{xs:'1rem', md:'2rem'}}}>
                    <Typography
                        href="#">
                        <IconButton sx={{color:'var(--color-red)'}}
                        >
                            <SchoolIcon sx={{fontSize:'2.5rem'}} />
                        </IconButton>
                    </Typography>
                    <Typography>
                    <Box sx={{display:'flex' , flexDirection:'column'}}>
                            <Typography component="span" fontWeight="500" sx={{ color:'var(--color-lilac)' , fontSize:{xs: '.8rem', md: '.9rem'}}} >
                            2020 - 2018
                            </Typography>
                            <Typography component="span" fontWeight="700" sx={{mt: '.5rem' , fontSize:{xs: '1.1rem', md: '1.4rem'}}} >
                            Bachelors Degree
                            </Typography>
                            <Typography component="span" fontWeight="normal" sx={{mt: '.5rem' , fontSize:{xs: '1rem', md: '1rem'}}} >
                            I studied for two years for a bachelor's degree in software engineering.
                            </Typography>
                            </Box>
                    </Typography>
                </Box>
                <Box  sx={{display:'flex' , flexDirection:'row' , gap:'2rem' , mt:'2rem'}}>
                    <Typography
                        href="#">
                        <IconButton sx={{color:'var(--color-red)'}}
                        >
                            <SchoolIcon sx={{fontSize:'2.5rem'}} />
                        </IconButton>
                    </Typography>
                    <Typography>
                    <Box sx={{display:'flex' , flexDirection:'column'}}>
                            <Typography component="span" fontWeight="500" sx={{ color:'var(--color-lilac)' , fontSize:{xs: '.8rem', md: '.9rem'}}} >
                            2018 - 2016
                            </Typography>
                            <Typography component="span" fontWeight="700" sx={{mt: '.5rem' , fontSize:{xs: '1.1rem', md: '1.4rem'}}} >
                            Associates Degree
                            </Typography>
                            <Typography component="span" fontWeight="normal" sx={{mt: '.5rem' , fontSize:{xs: '1rem', md: '1rem'}}} >
                            For two years, I studied for an associate degree in software.
                            </Typography>
                            </Box>
                    </Typography>
                </Box>
                </Box>
            <Box className='experience-box' sx={{ position:'relative' ,  background:'var(--color-purpel)' , p:'2rem' , borderRadius:'1rem' , display: 'flex', gap: '2rem', flexDirection: { xs: 'column', md: 'column' }, mt:{xs:'0',md:'2rem'} }}>
                {/* <Divider sx={{background:'var(--color-red)'}} orientation="vertical" flexItem /> */}
                <Box  sx={{display:'flex' , flexDirection:'row' , gap:{xs:'1rem', md:'2rem'}}}>
                    <Typography
                        href="#">
                        <IconButton sx={{color:'var(--color-red)'}}
                        >
                            <CodeIcon sx={{fontSize:'2.5rem'}} />
                        </IconButton>
                    </Typography>
                    <Typography>
                    <Box sx={{display:'flex' , flexDirection:'column'}}>
                            <Typography component="span" fontWeight="500" sx={{ color:'var(--color-lilac)' , fontSize:{xs: '.8rem', md: '.9rem'}}} >
                            2022 - Present
                            </Typography>
                            <Typography component="span" fontWeight="700" sx={{mt: '.5rem' , fontSize:{xs: '1.1rem', md: '1.4rem'}}} >
                            Front-End Developer
                            </Typography>
                            <Typography component="span" fontWeight="normal" sx={{mt: '.5rem' , fontSize:{xs: '1rem', md: '1rem'}}} >
                            I have experience working with several companies in the field of front end.
                            </Typography>
                            </Box>
                    </Typography>
                </Box>
                <Box  sx={{display:'flex' , flexDirection:'row' , gap:'2rem' , mt:'2rem'}}>
                    <Typography
                        href="#">
                        <IconButton sx={{color:'var(--color-red)'}}
                        >
                            <DesignServicesIcon sx={{fontSize:'2.5rem'}} />
                        </IconButton>
                    </Typography>
                    <Typography>
                    <Box sx={{display:'flex' , flexDirection:'column'}}>
                            <Typography component="span" fontWeight="500" sx={{ color:'var(--color-lilac)' , fontSize:{xs: '.8rem', md: '.9rem'}}} >
                            2020 - 2021
                            </Typography>
                            <Typography component="span" fontWeight="700" sx={{mt: '.5rem' , fontSize:{xs: '1.1rem', md: '1.4rem'}}} >
                            UI/UX Designer
                            </Typography>
                            <Typography component="span" fontWeight="normal" sx={{mt: '.5rem' , fontSize:{xs: '1rem', md: '1rem'}}} >
                            I have done ui design for a year at NoroWeb Company.
                            </Typography>
                            </Box>
                    </Typography>
                </Box>
                </Box>
            </Grid>
        </Container>
    );
}

export default Experience;
