import React from 'react';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box'
import Container from '@mui/material/Container';
import firstSample from '../images/anigah-sample.jpg'
import secondSample from '../images/hiva-sample.jpg'
import thirdSample from '../images/sayehban-sample.jpg'
import fourSample from '../images/minak-sample.jpg'
import fiveSample from '../images/neginrose-sample.jpg'
import sixSample from '../images/nikan.jpg'

const RecentWorks = () => {
    return (
        <Container id="recent-works" sx={{ mt: '4rem' }}>
            <Typography component="span" fontWeight='bold' sx={{ fontSize: { xs: '1.4rem', md: '2rem' } }}>
                Recent Works
            </Typography>
            <Grid sx={{ display: 'flex', gap: '2rem', flexDirection: { xs: 'column', md: 'row' } }}>
                <Box sx={{ display: 'flex' , gap: '2rem', mt: '2rem' , flexWrap:'wrap'}}>
                <Box className='recent-work-box' sx={{position:'relative' , width:{ xs: '100%', md: '48%'}}}>
                        <img className='recent-work-pic' src={sixSample} alt='' ></img>
                        <Typography href='https://nikanprofile.com/' component="a" fontWeight='bold' sx={{position:'absolute'  , top:'50%' , 
                        left:'50%' , transform:'translate(-50%, -50%)' , textAlign:'center' , 
                        fontSize: { xs: '1rem', md: '1.1rem', opacity: '0' } }}>
                            nikanprofile Site
                        </Typography>
                    </Box>
                    <Box className='recent-work-box' sx={{position:'relative' , width:{ xs: '100%', md: '48%'}}}>
                        <img className='recent-work-pic' src={firstSample} alt='' ></img>
                        <Typography href='https://www.anigah.com/' component="a" fontWeight='bold' sx={{position:'absolute'  , top:'50%' , 
                        left:'50%' , transform:'translate(-50%, -50%)' , textAlign:'center' , 
                        fontSize: { xs: '1rem', md: '1.1rem', opacity: '0' } }}>
                            Anigah Site
                        </Typography>
                    </Box>
                    <Box href='https://www.hiwa-net.com/' className='recent-work-box' sx={{position:'relative' , width:{ xs: '100%', md: '48%'} }}>
                        <img className='recent-work-pic' src={secondSample} alt='' ></img>
                        <Typography component="a" fontWeight='bold' sx={{position:'absolute' , top:'50%' , 
                        left:'50%' , transform:'translate(-50%, -50%)' , textAlign:'center' , 
                        fontSize: { xs: '1rem', md: '1.1rem', opacity: '0' } }}>
                        Hiva Site
                        </Typography>
                    </Box>
                    <Box className='recent-work-box' sx={{position:'relative' , width:{ xs: '100%', md: '48%'}}}>
                        <img className='recent-work-pic' src={thirdSample} alt=''></img>
                        <Typography href='https://www.sayehban.com/' component="a" fontWeight='bold' sx={{position:'absolute' , top:'50%' , 
                        left:'50%' , transform:'translate(-50%, -50%)' , textAlign:'center' , 
                        fontSize: { xs: '1rem', md: '1.1rem', opacity: '0' } }}>
                           Sayesazan Site
                        </Typography>
                    </Box>
                    <Box className='recent-work-box' sx={{position:'relative' , width:{ xs: '100%', md: '48%'}}}>
                        <img className='recent-work-pic' src={fourSample} alt=''></img>
                        <Typography href='http://minakco.ir/' component="a" fontWeight='bold' sx={{position:'absolute' , top:'50%' , 
                        left:'50%' , transform:'translate(-50%, -50%)' , textAlign:'center' , 
                        fontSize: { xs: '1rem', md: '1.1rem', opacity: '0' } }}>
                           Minakco Site
                        </Typography>
                    </Box>
                    <Box className='recent-work-box' sx={{position:'relative' , width:{ xs: '100%', md: '48%'}}}>
                        <img className='recent-work-pic' src={fiveSample} alt=''></img>
                        <Typography href='http://www.neginrose.com/' component="a" fontWeight='bold' sx={{position:'absolute' , top:'50%' , 
                        left:'50%' , transform:'translate(-50%, -50%)' , textAlign:'center' , 
                        fontSize: { xs: '1rem', md: '1.1rem', opacity: '0' } }}>
                            Negin Rose Site
                        </Typography>
                    </Box>
                </Box>
            </Grid>
        </Container>
    );
}

export default RecentWorks;
