import React from 'react';
import Container from '@mui/material/Container';
import Paper from '@mui/material/Paper'
import InstagramIcon from '@mui/icons-material/Instagram';
import GitHubIcon from '@mui/icons-material/GitHub';
import TelegramIcon from '@mui/icons-material/Telegram';
import EmailIcon from '@mui/icons-material/Email';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import IconButton from '@mui/material/IconButton';
import farimah_pic from '../images/farimah.jpg'
import back from '../images/back.png'
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box'
import Button from '@mui/material/Button';


const About = () => {
    const onMouseOverCaptureHandler = () => {
        const fcanimation =document.querySelector('.fc-animation');
        window.onmousemove = function(e){
        let x = e.clientX / 5;
        let y = e.clientY / 5;
        fcanimation.style.backgroundPositionX = x + "px";
        fcanimation.style.backgroundPositionY = y + "px";
      };
    }
    return (
        <Paper onMouseOver={onMouseOverCaptureHandler} className='fc-animation' sx={{backgroundImage: `url(${back})` , backgroundPosition:'center' , backgroundColor: 'var(--color-accent1)', boxShadow: 'unset', mx: 'auto', py: '5rem', textAlign: 'center' }}>
            <Container>
                <Typography color='white'>
                    <Box>
                        <img className='portfolio-pic' src={farimah_pic} alt='' ></img>
                        <Box sx={{display:'flex' , flexDirection:'column'}}>
                            <Typography component="span" fontWeight="700" sx={{mt: '1.5rem' , fontSize:{xs: '1.6rem', md: '2rem'}}} >
                                Farimah Chiani
                            </Typography>
                            <Typography component="span" fontWeight="normal" sx={{mt: '.5rem' , fontSize:{xs: '1rem', md: '1.1rem'}}} >
                                I'm a Web Developer
                            </Typography>
                            </Box>
                        <Box marginTop={1} sx={{ display:'flex' , justifyContent:'center' , gap:'.5rem'}}>
                            <a className='social-link'
                                href="https://www.instagram.com/farimah.chiani/">
                                <IconButton
                                    color="inherit"
                                    //sx={{mr: '.5rem'}}
                                    >
                                    <InstagramIcon />
                                </IconButton>
                            </a>
                            <a className='social-link'
                                href="https://github.com/farimah-chiani">
                                <IconButton
                                    color="inherit"
                                    //sx={{ mr: '.5rem'}}
                                    >
                                    <GitHubIcon />
                                </IconButton>
                            </a>
                            <a className='social-link'
                                href="mailto:chiani.farimah@yahoo.com?Subject=Hello%20Farimah">
                                <IconButton
                                    color="inherit"
                                    //sx={{ mr: '.5rem'}}
                                    >
                                    <EmailIcon />
                                </IconButton>
                            </a>
                            <a className='social-link'
                                href="https://t.me/FarimahChiani">
                                <IconButton
                                    color="inherit" 
                                    //sx={{mr: '.5rem'}}
                                    >
                                    <TelegramIcon />
                                </IconButton>
                            </a>
                            <a className='social-link'
                                href="www.linkedin.com/in/farimah-chiani">
                                <IconButton
                                    color="inherit"
                                    //sx={{ mr: '.5rem' }}
                                    >
                                    <LinkedInIcon />
                                </IconButton>
                            </a>
                        </Box>
                    </Box>
                    <Box marginTop={1}>
                    <Button variant="contained" sx={{background:'var(--color-red)' , fontSize:'1rem', 
                    borderRadius:'1.5rem' , fontWeight:'700' , px:'1.5rem' , py:'.5rem' , color:'var(--color-accent1)' }}><a
                    href="mailto:chiani.farimah@yahoo.com?Subject=Hello%20farimah" className='contact-btn'>Contact Me </a></Button>
                    </Box>
                </Typography>
            </Container>
        </Paper>
    );
}

export default About;
