import React from 'react';
import Button from '@mui/material/Button';
import Box from '@mui/material/Box'
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import Container from '@mui/material/Container';
import LinearProgressFirst from './LinearProgressFirst'
import LinearProgressSecond from './LinearProgressSecond'
import LinearProgressThird from './LinearProgressThird'
import farimah_pic from '../images/farimah-p.jpg'
import my_cv from '../file/FarimahChiani-CV.pdf'


const Describe = () => {
    return (

        <Container id="about-me" sx={{ mt: '4rem' }}>
            <Typography component="span" fontWeight='bold' sx={{ fontSize: { xs: '1.4rem', md: '2rem' } }}>
                About Me
            </Typography>
            <Box sx={{ display: 'flex' , alignItems:'center' , gap:{ xs:'3rem' , md:'4rem'}, flexDirection: { xs: 'column-reverse', md: 'row' } }}>
                <Box>
                    <img className='portfolio-pic' src={farimah_pic} alt='' ></img>
                </Box>
                <Grid className='aboutME-box' sx={{position:'relative' , alignItems: 'center', gap: '3rem', p: '3rem', borderRadius: '1rem', background: 'var(--color-purpel)', display: 'flex', flexDirection: { xs: 'column', md: 'row' }, mt: '2rem' }}>
                    <Box sx={{ width: {xs:'100%' , md:'50%' } }}>
                        <Typography sx={{ fontSize: '1.1rem' }}> I am Farimah Chiani, a graduate of software engineering, I have work experience in the fields of ui and front-end coding in Html, Sass, Css, Bootstrap, WordPress, Joomla, Java Script, Jquey, ReactJs, Angular and AgGrid, and I wrote this portfolio for myself with reactjs and material ui. </Typography>
                        <Box marginTop={2}>
                            <Button variant="contained" sx={{
                                background: 'var(--color-red)', fontSize: '1rem',
                                borderRadius: '1.5rem', fontWeight: '700', px: '1.5rem', py: '.5rem' , color:'var(--color-accent1)'
                            }}> <a
                            href={my_cv} target="_blank" rel='noopener noreferrer' className='cv-btn'>Download CV </a></Button>
                        </Box>
                    </Box>
                    <Box sx={{ display: 'flex', flexDirection: 'column', width:{xs:'100%' , md:'50%' }, gap: '1rem' }}>
                        <LinearProgressFirst />
                        <LinearProgressSecond />
                        <LinearProgressThird />
                    </Box>
                </Grid>
            </Box>
        </Container >
    );
}

export default Describe;
