import React from 'react';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box'
import Button from '@mui/material/Button';

const Contact = () => {
    return (
        <Container >
            <Typography color='white' sx={{my:'4rem' , textAlign:'center' , width:'70%' , mx:'auto'}}>
                <Box sx={{ display: 'flex', flexDirection: 'column' }}>
                    <Typography component="span" fontWeight="700" sx={{ mt: '1.5rem', fontSize: { xs: '1.6rem', md: '2rem' } }} >
                        Get In Touch!
                    </Typography>
                    <Typography component="span" fontWeight="normal" sx={{ mt: '.5rem', fontSize: { xs: '1rem', md: '1.1rem' } }} >
                        we can Talk via socal media and Yahoo
                        my inbox is always open and i'll try to get best Answer to your masage
                    </Typography>
                </Box>
                <Box marginTop={2}>
                     <Button variant="contained" sx={{background:'var(--color-red)' , fontSize:'1rem', 
                    borderRadius:'1.5rem' , fontWeight:'700' , px:'1.5rem' , py:'.5rem' , color:'var(--color-accent1)' }}><a
                    href="mailto:chiani.farimah@yahoo.com?Subject=Hello%20Farimah" className='contact-btn'>Contact Me </a></Button>
                </Box>
            </Typography>
        </Container>
    );
}

export default Contact;
