import * as React from 'react';
import PropTypes from 'prop-types';
import LinearProgress, { linearProgressClasses } from '@mui/material/LinearProgress';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import { styled } from '@mui/material/styles';

const BorderLinearProgress = styled(LinearProgress)(({ theme }) => ({
  height: 10,
  borderRadius: 5,
  [`&.${linearProgressClasses.colorPrimary}`]: {
    backgroundColor: theme.palette.grey[theme.palette.mode === 'light' ? 200 : 800],
  },
  [`& .${linearProgressClasses.bar}`]: {
    borderRadius: 5,
    backgroundColor: theme.palette.mode === 'light' ? '#1a90ff' : '#308fe8',
  },
}));

function LinearProgressSecond(props) {
  return (
    
    <Box sx={{ display: 'flex', alignItems: 'center', flexDirection: 'column-reverse' }}>
      <Box sx={{ width: '100%', mr: 1}} >
        {/* <LinearProgress sx={{backgroundColor:'yellow' , borderRadius:'.25rem'}} variant="determinate" {...props} /> */}
        <BorderLinearProgress variant="determinate" value={70} />
      </Box>
      <Box sx={{
        minWidth: 35, display: 'flex', flexDirection: 'row-reverse', color: 'withe',
        alignItems: 'center' , mb:'1rem' , width:'100%'
      }}>
        <Typography sx={{width:'40%' , textAlign:'right'}} variant="body2" >70%</Typography>
        <Typography sx={{width:'60%'}}>
        Web Development
        </Typography>
      </Box>
    </Box>
  );
}

LinearProgressSecond.propTypes = {
  /**
   * The value of the progress indicator for the determinate and buffer variants.
   * Value between 0 and 100.
   */
  value: PropTypes.number.isRequired,
};

export default function LinearWithValueLabel() {
  const [progress, setProgress] = React.useState(10);

  React.useEffect(() => {
    const timer = setInterval(() => {
      setProgress((prevProgress) => (prevProgress >= 100 ? 70 : prevProgress + 10));
    }, 800);
    return () => {
      clearInterval(timer);
    };
  }, []);

  return (
    <Box sx={{ width: '100%', color: 'white' }}>
      <LinearProgressSecond sx={{backgroundColor:'white' , height:'1rem' , borderRadius:'.25rem'}}  value={progress} />
    </Box>
  );
}