import React from 'react';
import Paper from '@mui/material/Paper'
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import service_1 from '../images/service-1.svg'
import service_2 from '../images/service-2.svg'
import service_3 from '../images/service-3.svg'
import Box from '@mui/material/Box'
import Container from '@mui/material/Container';


const ServiceBox = () => {
    return (
        <Container id="services" sx={{ mt: '4rem' }}>
            <Typography component="span" fontWeight='bold' sx={{ fontSize: { xs: '1.4rem', md: '2rem' } }}>
                Services
            </Typography>
            <Grid sx={{ display: 'flex', gap: '2rem', flexDirection: { xs: 'column', md: 'row' }, mt: '2rem' }}>
                <Paper className='service-box shadow-blue bg-blue' sx={{
                    display: 'flex'
                    , justifyContent: 'center', flexDirection: 'column', borderRadius: '1rem'
                    , color: 'white', alignItems: 'center', textAlign: "center" , color:'var(--color-accent1)', width:{ xs: '100%', md: '33%' }
                }}>
                    <Box padding='3rem'>
                        <img className='services-pic' src={service_1} alt=''></img>
                        <Box marginTop={2}>
                            <Typography component="span" fontWeight="600" sx={{ fontSize: { xs: '1.2rem', md: '1.3rem' } }}>
                                UI/UX design
                            </Typography>
                        </Box>
                        <Box marginTop={2}>
                            <Typography component="p" sx={{ fontSize: { xs: '1rem', md: '1.1rem' } }}>
                            I have done ui design for a year at NoroWeb Company.
                            </Typography>
                        </Box>
                    </Box>
                </Paper>
                <Paper className='service-box shadow-yellow bg-yellow' sx={{
                    display: 'flex'
                    , justifyContent: 'center', flexDirection: 'column', borderRadius: '1rem'
                    , color: 'white', alignItems: 'center', textAlign: "center" , color:'var(--color-accent1)', width:{ xs: '100%', md: '33%' }
                }}>
                    <Box padding='3rem'>
                        <img className='services-pic' src={service_2} alt=''></img>
                        <Box marginTop={2}>
                            <Typography component="span" fontWeight="600" sx={{ fontSize: { xs: '1.2rem', md: '1.3rem' } }}>
                            WordPress Development
                            </Typography>
                        </Box>
                        <Box marginTop={2}>
                            <Typography component="p" sx={{ fontSize: { xs: '1rem', md: '1.1rem' } }}>
                            I have experience working with several companies in the field of front end.
                            </Typography>
                        </Box>
                    </Box>
                </Paper>
                <Paper className='service-box shadow-pink bg-pink' sx={{
                    display: 'flex'
                    , justifyContent: 'center', flexDirection: 'column', borderRadius: '1rem'
                    , color: 'white', alignItems: 'center', textAlign: "center" , color:'var(--color-accent1)', width:{ xs: '100%', md: '33%' }
                }}>
                    <Box padding='3rem'>
                        <img className='services-pic' src={service_3} alt=''></img>
                        <Box marginTop={2}>
                            <Typography component="span" fontWeight="600" sx={{ fontSize: { xs: '1.2rem', md: '1.3rem' } }}>
                            Web Development
                            </Typography>
                        </Box>
                        <Box marginTop={2}>
                            <Typography component="p" sx={{ fontSize: { xs: '1rem', md: '1.1rem' } }}>
                            As needed, I have experience designing posters and images using Photoshop.
                            </Typography>
                        </Box>
                    </Box>
                </Paper>
            </Grid>
        
        </Container>
    );
}

export default ServiceBox;
